.products-section {
  max-width: 1200px;
  /* выравнивание по центру */
  margin: 0 auto;

  display: grid;
  grid-template-rows: min-content min-content;
}

.products-section h2 {
  text-align: center;
  justify-self: left;
}

.products {
  max-width: 1200px;
  margin-left: 20px;
  margin-right: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}


.search {
  max-width: 1200px;
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 20px;
  margin-bottom: 0px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.search h2 {
  justify-self: start;
  font-size: 32px;
}

.search-block {
  justify-self: right;
  border: 1px solid #9f9f9f;
  border-radius: 10px;
  padding: 15px;
  width: 250px;
}

.search-block:hover {
  border: 1px solid #001aff;
}

.search-block img {
  width: 13px;
  margin-right: 15px;
}

.search-block input {
  border: none;
  font-size: 16px;
}

.search-block input:focus-visible {
  outline: none;
  border-bottom: 1px solid #001aff;
}